.App {
  text-align: center;
  margin: auto;
  min-height: 0;
}

.App-link {
  color: #61dafb;
}

nav{
  top: 0;
  left: 0;
  position: absolute;
  opacity: 0.12;
  background-color: var(--gray1);
  width: 100vw;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  transition: opacity 0.52s;
  box-shadow: rgba(17, 17, 26, 0.1) 0px 8px 24px, rgba(17, 17, 26, 0.1) 0px 16px 56px, rgba(17, 17, 26, 0.1) 0px 24px 80px;
}

nav:hover{
  opacity: 1.0;
  cursor: pointer;
  transition: opacity 0.2s;
}


nav > *{
  margin: 2px 4.4px;
}

.left-nav{
  display: flex;
  flex-direction: row;
  align-items: center;
}

.back-to-main{
  font-family: 'Segoe UI', 'Gill Sans' ,Tahoma,  Verdana, sans-serif;
  font-weight: bolder;
  font-size: 1.52rem;
  padding: 2px 6.4px;
  border-radius: 8px;
  transition: background-color 0.8s ease-out, border 1.6s ease-out;
  -webkit-transition: background-color 0.8s ease-out, border 0.8s ease-out;
}

.back-to-main:hover{
  color: white;
  background-color: var(--blue);
  transition: background-color 0.8s ease-out, border 0.8s ease-out;
  -webkit-transition: background-color 0.8s ease-out, border 0.8s ease-out;
}

.normal-tool{
  margin: 4px 8px;
  color: var(--blue);
  padding: 4px 8px;
  border-radius: 8px;
  transition: background-color 0.8s ease-out, border 1.6s ease-out;
  -webkit-transition: background-color 0.8s ease-out, border 0.8s ease-out;
}

.normal-tool:hover{
  color: white;
  background-color: var(--blue);
  transition: background-color 0.8s ease-out, border 0.8s ease-out;
  -webkit-transition: background-color 0.8s ease-out, border 0.8s ease-out;
}

a{
  color: inherit;
  text-decoration: none;
}

a:visited{
  color: inherit;
}

.pfp-image-container{
  border: 2px solid var(--blue);
  height: fit-content;
  padding: 2px 2px;
  border-radius: 32px;
  background-color: transparent;
  transition: background-color 0.8s ease-out, border 1.6s ease-out;
  -webkit-transition: background-color 0.8s ease-out, border 0.8s ease-out;
}

.pfp-image-container:hover{
  box-shadow: rgba(58, 142, 253, 0.25) 0px 54px 55px, rgba(58, 142, 253, 0.12) 0px -12px 30px, rgba(58, 142, 253, 0.12) 0px 4px 6px, rgba(58, 142, 253, 0.17) 0px 12px 13px, rgba(58, 142, 253, 0.09) 0px -3px 5px;
  background-color: var(--blue);
  transition: background-color 0.8s ease-out, border 0.8s ease-out;
  -webkit-transition: background-color 0.8s ease-out, border 0.8s ease-out;
}


.profile-pic{
  width: 36px;
  transition: filter 2s linear;
  -webkit-transition: filter 2s linear;

}

.profile-pic:hover{
  filter: invert(100%);
  transition: filter 0.8s linear;
  -webkit-transition: filter 0.8s linear;
}
