.property-calculators{
    margin: auto;
    display: flex;
    flex-direction: column;
    align-items: center;
    font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
    flex-basis: 100%;
    color: var(--black);
}

.calculator{
    margin: auto;
    display: flex;
    flex-direction: column;
    border: 4px solid var(--blue);
    align-items: center;
    justify-content: space-between;
    margin: 8px;
    border-radius: 12px;
    max-width: 350px;
    padding: 8px;
    background-color: white;
    box-shadow: rgba(50, 50, 93, 0.25) 0px 13px 27px -5px, rgba(0, 0, 0, 0.3) 0px 8px 16px -8px;
}

.calculator-title{
    font-size: 1.6rem;
    font-weight: bolder;
    margin: 6.4px;
    text-decoration: underline 2px var(--blue);
}

.main-container{
    margin: 8px;
    /* width: 80%; */
}

.sub-container{
    display: flex;
    flex-direction: row;
    margin: 4px;
    justify-content: space-between;
    align-items: center;
}

.sub-container > *{
    margin: 2px;
}

label{
    text-align: left;
}

input{
    margin-left: 8px;
    border: 2px solid var(--gray1);
    background-color: white;
    outline: none;
    font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
    font-size: 0.96rem;
    height: fit-content;
    padding: 2px 4px;
    border-radius: 8px;
    width: 10rem;
}

input:focus{
    border: 2px solid var(--blue);
}

.normal-label{
    font-size: 0.88rem;
}

.fees-calculation{
    font-size: 0.88rem;
}

.final-total{
    font-weight: bold;
    color: var(--blue);
}

.total-container{
    padding: 4px;
    margin-top: 12px;
    border: 2px solid var(--blue);
    border-radius: 8px;
    visibility: visible;
    position: static;
}

.total-label{
    color: var(--blue);
    text-align: left;
}

.sub-total-label{
    font-size: 0.92rem;
}

.minify{
    font-size: 0.6rem;

}

.clear-all-container{
    margin: 4px;
    padding: 4px;
}

button{
    font-size: 0.8rem;
    color: white;
    background-color: var(--blue);
    font-weight: bold;
    font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
    border: 2px solid var(--blue);
    border-radius: 8px;
}

button:hover{
    background-color: var(--black);
}

button:active{
    box-shadow: rgba(50, 50, 93, 0.25) 0px 30px 60px -12px inset, rgba(0, 0, 0, 0.3) 0px 18px 36px -18px inset;
}

.key-container{
    margin: 16px 4px 4px;
}

.key-input{
    border-color: var(--gray2);
}

.calculation-container{
    /* border: 2px solid var(--gray2); */
    border-radius: 12px;
    background-color: var(--blue);
    border: none;
    margin: 12px 4px;
}



.calculation-container > .total-container{
    margin: 0px;
    background-color: white;
    color: var(--blue);
    border:2px solid var(--blue);
}

.subcalculation-container{
    border: none;
    border-radius: 12px;
    padding: 4px 8px 2.4px;
    display: flex;
    background-color: transparent;
    color: white;
    justify-content: space-between;
}

.clear-all-container{
    margin: 24px 0px 4px;
    display: flex;
    flex-direction: row;
    justify-content: left;
}

.expansion-label{
    font-size: 0.88rem;
    visibility: visible;
    position: static;
    transition: font-size 0.2s linear;
    -webkit-transition: font-size 0.2s linear;
}

.expansion-hidden{
    font-size: 0;
    visibility: hidden;
    position: absolute;
    transition: font-size 0.2s linear;
    -webkit-transition: font-size 0.2s linear;
}